.footer{
    width: 100%;
    padding: 6rem 0;
    background-color: rgba(19, 19, 19, 0.8);
}
.footer-container{
    max-width: 1140px;
    display: grid;
    margin: auto;
    grid-template-columns: repeat(2,1fr);
    gap: 40px;
}
.left-footer{
    height: 100%;
    max-width: 100%;
    display: flex   ;
    flex-direction: column;
    padding: 1rem;
    gap: 1.4rem;
}
.left-footer div{
    display: flex;
    align-items: center;
}
.right{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}
.social{
    margin-top: 1rem;
}
h4{
    font-size: 1.2rem;
    padding-bottom: 0.7rem;
}
p{
    font-size: 1.2rem;
    color: rgba(255, 255, 255, 0.932);
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}
@media screen and (max-width:640px) {
    .footer-container{
        grid-template-columns: 1fr;
        margin: auto;
        text-align: center;
        
    }
    
}