.about{
    width: 100%;
    margin: 3rem 0;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap:wrap ;
    height: 100%;
    padding: 30px 0px;
}
.left-about{
    text-align: center;
    margin: auto;
    padding: 1rem;
    max-width: 350px;
}
.left-about p{
    margin: 1.2rem 0;
}
.right-about{
    max-width: 700px;
    margin-left: 80px;
}
.right-about .img-container{
    display: grid;
    grid-template-columns:repeat(2,1fr) ;
    position: relative;
    align-items: center;
    text-align: center;

}
.right-about .img{
    width: 50vh;
    border: 1px solid #3333;
    height: 35vh;
}
.bottom{
    position: absolute;
    top: 60px;
    left: -90px;
}
@media screen and (max-width:786px) {
    .bottom{
        left: -60px;

    }
    .right-about .img{
        width: 40vh;
        border: 1px solid #3333;
        height: 30vh;
    }
    .right-about{
        margin: 40px 120px;
        width: 100%;
    }
    
}
