.project-heading{
    text-align: center;
    padding: 4rem 0 2rem 0;
}
.projects-container{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap:40px ;
    padding-bottom: 5rem;
}
.project-card{
    background: #1a1919;
    padding: 1.2rem 1rem;
    width: 100%; 
}
.project-title{
    color: white;
    padding: 1rem 0;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-weight: 400;
    font-size: 1.5rem;
}
.project-details p{
    padding-bottom: 1rem;
    font-size: 1.1rem;
    text-align: justify;    
}
.pro-btns{
    display: flex;
    justify-content: space-between;
    padding: 0.5rem 0;
    margin: 0.7rem 0;
}
.pro-btns .btn{
    padding: 0.5rem 1rem;
}
.project-card img{
    width: 100%;
    height: 40vh;
}
@media screen and (max-width:740px) {
    .projects-container{
        max-width: 90%;
        margin: auto;
        grid-template-columns: 1fr;
    }
}