.pricing{
    width: 100%;
    padding: 8rem 0rem ;
}
.card-container{
    max-width: 1140px;
    margin: auto;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 40px;
    display: grid;
}
@media screen and (max-width:740px) {
    .card-container{
        max-width: 90%;
        margin: auto;
        grid-template-columns: 1fr;
    }
}
.card{
    border: 1px solid #eeee;
    color: #eee;
    text-align: center;
    padding: 1rem;

}
.card:hover{
    background-color: #141414;
}
.card h3{
    font-size: 1.6rem;
    padding: 1rem;
}
.card p{
    padding: 10px 0;
}
.bar{
    border-bottom: 2px solid #eee;
    width: 10%;
    margin: 1.3rem auto;
    display: block;
}
.btc{
    font-size: 3rem;
    font-weight: 600;
    padding: 1rem 0;
}
.card .btn{
    display: block;
    width: 80%;
    margin: 2rem auto;
}